import React from 'react';
import ScrollToTop from 'react-scroll-up';
import FooterTwo from "./component/footer/FooterTwo";
import {FiChevronUp} from "react-icons/fi";
import Header from "./component/header/HeaderFour";
import TabThree from "./elements/tab/TabThree";
import Helmet from "./component/common/Helmet";
import Sketch from "react-p5";

const App = () => {  

    let grid, next, cols, rows
    let resolution = 14;
    let count = 0
    let totcount = 0

    const windowResized = (p5) => {
        p5.resizeCanvas(p5.windowWidth, p5.windowHeight);
    }
 
   const setup = (p5, canvasParentRef) => {
       let canvas = p5.createCanvas(p5.windowWidth, p5.windowHeight).parent(canvasParentRef);
       cols = Math.floor(p5.windowWidth / resolution);
       rows = Math.floor(p5.windowHeight / resolution);
       grid = [...Array(cols)].map(e => [...Array(rows)].map(value => Math.floor(2*Math.random())));
       canvas.position(0,0);
       canvas.style('z-index','-1')
   }

   const draw = (p5) => {
       count += 1
       totcount += 1
       p5.background(248, 249, 252);
       for (let i = 0; i < cols; i++) {
         for (let j = 0; j < rows; j++) {
           let x = i * resolution
           let y = j * resolution
           if (grid[i][j] === 1) {
             p5.fill([197, 158, 187,50])
             p5.stroke([81, 4, 114, 80]);
             //p5.fill([123, 203, 183,50] )
             //p5.stroke([123, 203, 183,80] );
             p5.rect(x, y, resolution - 1, resolution - 1)
           }
         }
       }
       next = grid.map(rows => rows.map(value => 0))
       for (let i = 0; i < cols; i++) {
         for (let j = 0; j < rows; j++) {
           let status = grid[i][j]
           let neighbors = countNeighbors(grid, i, j, cols, rows)
           if (status === 0 && neighbors === 3) {
             next[i][j] = 1
           } else if (status === 1 && (neighbors < 2 || neighbors > 3)) {
             next[i][j] = 0
           } else {
             next[i][j] = status
           }  
        }
       }
       grid = next;
         
       if (count === 400) {
           p5.noLoop()
       }
       
   }

   const countNeighbors = (grid, x, y, cols, rows) => {
       let sum = 0
       for (let i = -1; i < 2; i++) {
       for (let j = -1; j < 2; j++) {
           let xIndex = (x + i + cols) % cols
           let yIndex = (y + j + rows) % rows
           sum += grid[xIndex][yIndex]
       }
       }
       sum -= grid[x][y];
       return sum;
   }
  
    const SlideList = [
        {
            textPosition: 'text-center',
            category: 'Front-End Developer',
            description: '',
            buttonText: '',
            buttonLink: ''
        }
    ]


    return(
                <div>
                <Helmet pageTitle="Taylor Epskamp" />
                {/* Start Header Area  */}
                <Header headerPosition="header--static" logo="symbol-dark" color="color-black"/>
                {/* End Header Area  */}
                
                

                <div className="slider-wrapper">
                <Sketch style = {{position:'relative'}} setup={setup} draw={draw} windowResized={windowResized} />
                    {/* Start Single Slide */}
                    {SlideList.map((value , index) => (
                        <div className="slide designer-portfolio slider-style-3  pt--60 d-flex align-items-top justify-content-center  rn-slider-height" key={index}>
                            <div className="container">
                            
                                        <div className={`inner ${value.textPosition}`}>
                                            {value.category ? <span>{value.category}</span> : ''}
                                            <h2 className ="title" style ={{color: '#510572'}}>Taylor Epskamp</h2>
                                            {/* <h2 >based in Seattle.</h2> */}
                                            
                                        </div>  
                            </div>
                        </div>
                    ))}
                    {/* End Single Slide */}
                </div>
                
                {/* Start Designer Portfolio  */}
                <div className="designer-portfolio-area ptb--60 bg_color--1">
                    <div className="wrapper plr--70 plr_sm--30 plr_md--30">
                        <TabThree column="col-lg-4 col-md-6 col-sm-6 col-12" />
                    </div>
                </div>
                {/* End Designer Portfolio  */}

                {/* Start Footer Style  */}
                <FooterTwo />
                {/* End Footer Style  */}
                
                {/* Start Back To Top */}
                <div className="backto-top">
                    <ScrollToTop showUnder={160}>
                        <FiChevronUp />
                    </ScrollToTop>
                </div>
                {/* End Back To Top */}

            </div>
)}


export default App


